:root {
    --navbarBackground: rgb(148,0,211);
    --text: #fff;
}

* {
    font-family: "Roboto", sans-serif;
  }

.home {
    width: 100%;
    height: auto;
    align-items: center;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
    display: flex;
    flex-direction: column;
  }
 
.gradient {
    background: linear-gradient(90deg, rgba(57,0,86,1) 0%, rgba(78,0,71,1) 100%);    
    -webkit-background-clip: text;    
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.about {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
    color: #f0f0f0;
  }
  
.about h2 {
    font-size: 3rem;
    color: --text;
    height: 2rem;
  }

.about .aidanImage {
  width: 15vw;
  border: solid 0.25rem #fff;
  border: solid 0.25rem var(--text);
  border-radius: 50%;
}

@media only screen and (max-width: 600px) {
  .about .aidanImage {
      width: 40vw;
  }
}
  
.about .prompt {
    width: 90%;
    font-size: 2rem;
  }

.prompt svg {
    font-size: 60px;
    margin: 5px;
    color: white;
  }

.prompt svg:hover {
    font-size: 60px;
    margin: 5px;
    color: rgba(57,0,86,1);
  }
  
.skills {
    font-size: 35px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
.skills h1 {
    color: #3e497a;
  }
  
.skills .list {
    list-style: none;
    width: 100%;
    padding: 0%;
  }
  
.list span {
    font-size: 20px;
  }
  
@media only screen and (max-width: 600px) {
.about h2 {
    font-size: 40px;
}
  
.about .prompt {
    margin-top: 1.2rem;
    font-size: 20px;
    width: 80%;
}

.skills {
    text-align: center;
}
.list {
    padding: 0;
}

.skills h2 {
    font-size: 30px;
}
}
.projects {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .projects .projectTitle {
    text-align: center;
    color: #3e497a;
    font-size: 60px;
    font-weight: bolder;
  }
  
  .projectList {
    width: 70vw;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  }
  
  /* MENU ITEM STYLING */
  
  .projectItem {
    border-radius: 15px;
    width: 300px;
    height: 300px;
    margin: 40px;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    color: white;
    text-align: center;
  }
  .projectItem:hover {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    cursor: pointer;
  }
  
  .projectItem .bgImage {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    width: 100%;
    height: 200px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .projectItem h1 {
    font-size: 25px;
  }
  
  @media only screen and (max-width: 1300px) {
    .projectList {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media only screen and (max-width: 800px) {
    .projectList {
      grid-template-columns: 1fr;
    }
  }
  
  @media only screen and (max-width: 800px) {
    .projectList {
      width: 100%;
    }
    .projectItem {
      width: 300px;
      height: 300px;
    }
  }
:root {
  --text: #fff
}

* {
    font-family: "Roboto", sans-serif;
  }

.navbar {
    width: 100%;
    height: 100px;
    background-color: #4158D0;
    background-image: linear-gradient(90deg, #4158D0 0%, #C850C0 100%);
  }
  
  .links {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    margin: 2rem;
    font-size: 1.6rem;
  }
  
  .toggleButton {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .toggleButton svg {
    font-size: 50px;
  }
  .toggleButton button {
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  #open {
    height: 100vh;
  }
  
  #open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
  }
  
  #open a {
    width: 100%;
    text-align: center;
  }
  
  @media only screen and (max-width: 900px) {
    .navbar a {
      width: 80vw;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .toggleButton {
      display: flex;
    }
    #close a {
      display: none;
    }
  }
  
  @media only screen and (min-width: 601px) {
    .toggleButton {
      display: none;
    }
  
    .hiddenLinks {
      display: none;
    }
  
    #open {
      height: 100px;
    }
  
    #open .links {
      flex-direction: row;
    }
  }
